.telechart2-root {
  display: block;
  position: relative;
}

.telechart2-root,
.telechart2-root * {
  font-family: 'Roboto', Arial, sans-serif;
}

.telechart2-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
  padding: 20px 12px 0;
  color: black;
}

.telechart2-header .telechart2-root_theme_dark.telechart2-header,
.telechart2-root_theme_dark .telechart2-header {
  color: white;
}

@media screen and (max-width: 768px) {
  .telechart2-header {
    user-select: none;
  }
}

.telechart2-title {
  font-weight: 600;
  font-size: 16px;
  transition: 0.3s cubic-bezier(0.54, -0.01, 0.23, 0.98);
}

.telechart2-series-canvas {
  display: block;
}

.telechart2-navigation {
  display: block;
  position: relative;
  margin-top: 8px;
  height: 44px;
}

.telechart2-navigation-series-canvas {
  display: block;
  position: absolute;
  width: 100%;
  top: 2px;
  left: 0;
}

.telechart2-navigation-ui-canvas {
  display: block;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}

